* {
  box-sizing: border-box;
}

body {
  color: #212121;
  background-color: #e0d4e9;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.box {
  width: 100vw;
  height: 10vh;
  background-color: #7e7ed6;
  margin: 0;
  position: relative;
}

.search-box {
  font: inherit;
  max-width: 320px;
  height: 40px;
  background-color: #fff;
  border: 1px #fff;
  border-radius: 5px;
  padding: 5px;
  transition: all 1s ease-out;
  position: absolute;
  top: 15px;
  left: 40vw;
}

.search-box:hover {
  background-color: #e6e6fa;
  border: 1px #fff;
}

.search-box:focus {
  background-color: #e6e6fa;
  outline: none;
}

.search-image {
  background-color: #948bc1;
  border-radius: 50%;
  position: absolute;
  top: 20px;
  left: 50vw;
}

